body {
  --color-active: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 400;
}

.header {
  box-sizing: border-box;
  background-color: #fff;
  width: 100%;
  padding-left: 50px;
  padding-right: 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.img {
  width: 40px;
  height: 40px;
}

nav {
  justify-content: flex-end;
  margin-left: auto;
  display: flex;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

ul :not(:last-child) {
  margin-right: 20px;
}

.list-item {
  color: var(--color-active);
  display: flex;
  position: relative;
}

.nav-link {
  color: #000;
  text-transform: uppercase;
  word-wrap: break-word;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.nav-link:hover {
  color: var(--color-active);
}

.is-active {
  color: var(--color-active);
  flex-direction: column;
}

.is-active:after {
  content: "";
  background: var(--color-active);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  top: calc(100% - 4px);
}

